.weather-card-wrap {
  margin-top: 2rem;
  width: 100%;
  max-width: 450px;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.3);
  border-radius: 10px !important;
  margin: 0 auto;
}
.weather-card-wrap + .weather-card-wrap {
  margin-top: 10px;
}

.weather-title {
  font-size: 40px;
  font-weight: 300;
  line-height: 1.2;
  text-align: center;
}
.weather-degree-details {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.weather-degree {
  font-size: 50px;
  font-weight: 300;
  line-height: 1.2;
  padding-right: 20px;
}
.weather-clouds {
  margin: 0;
  text-align: center;
  font-size: 20px;
  line-height: 1.2;
  font-weight: 350;
}
.weather-feelslike {
  text-align: center;
  font-size: 20px;
  line-height: 1.2;
  font-weight: 350;
  margin: 0;
}
.weather-card-close {
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 0;
  margin: 15px 15px 0 0;
  transform: scale(1.1);

  transition: all 0.1s ease;
}
.weather-card-close:hover {
  transform: scale(1.4);
}

.weather-card-reload {
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 0;
  margin: 15px 35px 0 0;
}

.weather-card-reload:hover {
  animation: rotatingWithScale 0.5s linear forwards;
}
@keyframes rotatingWithScale  {
  0% {
    transform: scale(1) rotate(0deg);
  }
  20% {
    transform: scale(1.4);
  }
  100% {
    transform: scale(1.4) rotate(360deg);
  }
}
